@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bungee&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root, .App {
  height: 100%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: 'Roboto Slab', sans-serif;
  color: #2f2d2d;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: #ccc9c9; }

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.homeScreen, .gameScreen, .statsScreen {
  background-color: #e6dee9; }

.homeScreen {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  font-weight: 700;
  align-items: center; }
  .homeScreen .header {
    width: 100%;
    height: 104px;
    background-color: #826090;
    color: #f8f6f9;
    display: flex;
    justify-content: center;
    align-items: center; }
    .homeScreen .header h1 {
      text-align: center;
      font-family: 'Bungee', cursive;
      font-size: 36px; }
  .homeScreen .menuItems {
    display: flex;
    flex-direction: column;
    padding: 32px 52px;
    padding-bottom: 72px;
    flex: 1 1;
    width: 100%;
    max-width: 500px; }
    .homeScreen .menuItems .startBtn {
      text-transform: uppercase;
      border-radius: 8px;
      height: 72px;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 -3px 0 0 #2545a8 inset;
      background-color: #6683dd;
      color: #f8f6f9;
      font-size: 32px;
      padding: 16px;
      margin-top: auto; }
      .homeScreen .menuItems .startBtn.active {
        background-color: #4669d6;
        box-shadow: 0 -2px 0 0 #1e3888 inset; }
    .homeScreen .menuItems .menuItem {
      background-color: #575353;
      box-shadow: 0 -3px 0 0 #393636 inset;
      color: #f8f6f9;
      padding: 16px;
      border-radius: 8px;
      box-sizing: inherit;
      line-height: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 20px;
      margin-top: 12px;
      text-transform: uppercase;
      -webkit-transition: all 0.1s ease;
      transition: all 0.1s ease; }
      .homeScreen .menuItems .menuItem.disabled {
        opacity: 0.6; }
      .homeScreen .menuItems .menuItem.active {
        background-color: #2f2d2d;
        box-shadow: 0 -2px 0 0 #1b1a1a inset; }
    .homeScreen .menuItems .modeSelector {
      display: grid;
      grid-template-columns: 1fr 3fr 1fr;
      grid-gap: 12px;
      width: 100%;
      font-size: 28px; }

.statsScreen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
  padding: 72px 52px; }
  .statsScreen table {
    border-collapse: collapse; }
    .statsScreen table th {
      text-align: right; }
    .statsScreen table tr {
      border-bottom: 1px solid #575353; }
      .statsScreen table tr:last-of-type {
        border-bottom: none; }
      .statsScreen table tr th {
        border-right: 1px solid #575353;
        width: 66%; }
      .statsScreen table tr th, .statsScreen table tr td {
        padding: 8px 16px; }
  .statsScreen .backHomeBtn {
    font-weight: 700;
    background-color: #575353;
    box-shadow: 0 -3px 0 0 #393636 inset;
    color: #f8f6f9;
    padding: 16px;
    border-radius: 8px;
    box-sizing: inherit;
    line-height: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    margin-top: 12px;
    text-transform: uppercase;
    margin-top: auto; }
    .statsScreen .backHomeBtn.disabled {
      opacity: 0.6; }
    .statsScreen .backHomeBtn.active {
      background-color: #2f2d2d;
      box-shadow: 0 -2px 0 0 #1b1a1a inset; }
  .statsScreen .retryBtn {
    font-weight: 700;
    text-transform: uppercase;
    border-radius: 8px;
    height: 72px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 -3px 0 0 #2545a8 inset;
    background-color: #6683dd;
    color: #f8f6f9;
    font-size: 32px;
    padding: 16px;
    margin-top: 12px; }
    .statsScreen .retryBtn.active {
      background-color: #4669d6;
      box-shadow: 0 -2px 0 0 #1e3888 inset; }

.gameScreen {
  max-height: 900px;
  padding: 0 32px;
  width: 100%;
  height: 100%;
  max-width: 480px;
  display: flex;
  flex-direction: column; }
  .gameScreen .gameHeader {
    font-weight: 700;
    height: 104px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%; }
    .gameScreen .gameHeader .title {
      font-size: 28px; }
    .gameScreen .gameHeader .cancelBtn {
      background-color: #575353;
      box-shadow: 0 -2px 0 0 #393636 inset;
      color: #f8f6f9;
      height: 32px;
      padding: 0 12px;
      font-size: 15px;
      border-radius: 8px;
      border: none;
      font: inherit;
      line-height: 1;
      display: flex;
      align-items: center; }
  .gameScreen .boardContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    flex: 1 1; }
    .gameScreen .boardContainer .board {
      width: 100%;
      display: grid;
      grid-gap: 8px;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr; }
      .gameScreen .boardContainer .board .cell {
        border-radius: 8px;
        background-color: #6D435A;
        color: #f8f6f9;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 60px;
        -webkit-transition: all 0.1s ease;
        transition: all 0.1s ease;
        box-shadow: 0 -3px 0 0 #492d3c inset;
        line-height: 1; }
        .gameScreen .boardContainer .board .cell.active {
          background-color: #613c50;
          box-shadow: 0 -1px 0 0 #3d2632 inset; }
        .gameScreen .boardContainer .board .cell .displayLetter {
          opacity: 0; }
          .gameScreen .boardContainer .board .cell .displayLetter.show {
            -webkit-transition: all 0.2s ease-in-out;
            transition: all 0.2s ease-in-out;
            opacity: 1; }
        .gameScreen .boardContainer .board .cell::before {
          content: "";
          padding-bottom: 100%;
          display: inline-block;
          vertical-align: top; }
  .gameScreen .progressBarHolder {
    width: 100%;
    position: relative;
    border-radius: 4px;
    background-color: #ccc9c9;
    box-shadow: 0 -1px 0 0 #b9b5b5 inset;
    margin-top: 32px;
    height: 8px;
    min-height: 8px;
    display: flex; }
    .gameScreen .progressBarHolder svg {
      border-radius: 4px;
      height: 7px;
      -webkit-filter: drop-shadow(0px 1px 0px #1e3888);
              filter: drop-shadow(0px 1px 0px #1e3888); }
    .gameScreen .progressBarHolder .progressBar {
      position: absolute;
      width: 100%; }
  .gameScreen .btnRow {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 12px;
    margin-top: 32px; }
    .gameScreen .btnRow .matchBtn {
      border-radius: 8px;
      height: 52px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 18px;
      font-weight: 700;
      background-color: #f8f6f9;
      box-shadow: 0 -3px 0 0 #ccc9c9 inset; }
      .gameScreen .btnRow .matchBtn.active {
        background-color: #6683dd;
        box-shadow: 0 -2px 0 0 #2545a8 inset;
        color: #f8f6f9; }
      .gameScreen .btnRow .matchBtn.disabled {
        opacity: 0.5; }
  .gameScreen .pager {
    height: 104px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-size: 20px;
    justify-content: center; }
    .gameScreen .pager .number {
      flex: 1 1; }
      .gameScreen .pager .number:first-of-type {
        text-align: right; }

@media (max-width: 400px) {
  .homeScreen .menuItems {
    padding-left: 32px;
    padding-right: 32px; }
  .gameScreen {
    padding-left: 24px;
    padding-right: 24px; } }

@media (max-width: 320px) {
  .gameScreen .displayLetter {
    font-size: 52px; }
  .gameScreen .btnRow .matchBtn {
    font-size: 15px; }
  .gameScreen .pager {
    font-size: 20px; } }

